import axios from 'axios';
import { writable } from "svelte/store";

//1. 
const setPromise = (url) => { 
    const options = {
		method: 'GET',
		url: url,
		params: {language: 'ko', page: '1'},
		headers: {
			accept: 'application/json',
    		Authorization: 'Bearer eyJhbGciOiJIUzI1NiJ9.eyJhdWQiOiIwM2NiNTExOGRhMjg4ZGFjNWNjN2M5YWJmMDhmMzA3OCIsIm5iZiI6MTc2MTYzNTE0MC40OTIsInN1YiI6IjY5MDA2YjQ0YzA2M2JkNDM2ZDNjMDA2NSIsInNjb3BlcyI6WyJhcGlfcmVhZCJdLCJ2ZXJzaW9uIjoxfQ.A3LceISJ21aN5YrFKQWXAe-jBp8zbV-hrvSdPrSe-yY'
		}
	};
	const getPromise = async () => {
		try {
			const res = await axios.request(options);
			return res;
		}catch(error) {
			throw new Error(error);
		}
	};

    const { subscribe } = writable(getPromise());

    return {
        subscribe,
    }
}

